export const PROJECT_BLACKJACK_DESCRIPTION = "Dette er et prosjekt som ble påbegynt i Scrimba sitt javascript kurs som jeg tok videre og bygget på med nye funksjoner som f.eks at man kan vedde mot dealeren og man kan skrive inn navnet sitt. Har og implementert litt flere regler fra faktisk Blackjack, noe som viste seg å være minst like utfordrende som selve kodingen. Har også påbegynt det å implementere en API som heter \"Deckofcards\" for å gjøre spillet litt mer livlig med å ha faktiske kort på bordet. Prosjektet omfatter veldig mye Javascript og er ikke 100% fullført enda.";

